import angular from "angular";
import conf from "../../conf";
import confHttp from "./confHttp.js";
import confNormalizeHttpResp from "./confNormalizeHttpResp.js";

/**
 *  http 
 *
 *  https://docs.angularjs.org/api/ng/service/$http#interceptors
 *
 *  Angular factory httpJSONJSON
 *
 * {
 *      code    : "",        // ,  : "SUCCESS", "ERROR"
 *      msg     : "",        //  JS 
 *      rawMsg  : "",        // 
 *      data    : ...,       // key "data"
 *      ...     : ...        // 
 * }
 *
 *  skipNomorelize=false 
 *
 *
 * 1. 200:
 *    1.1 JSON :
 *         {code:"SUCCESS"}
 *    1.2  JSON
 *    1.3  JSON ,  code == "SUCCESS",  raw msg
 *    1.4  JSON ,  code != "SUCCESS",  code  "ERROR"  raw msg.
 *
 * 2. 200
 *    2.1 JSON : {code:"ERROR", msg:""}
 *    2.2  JSON
 *    2.3  JSON ,  raw msg.
 *
 * 3. raw msg 
 *    3.1 {msg:"xxxStackTrace", raw:true}  => {msg:"", rawMsg: "xxxStackTrace"}
 *    3.2 {msg:"yyyyyy", raw:false}  => {msg:"yyyyyy"}
 *
 * @param _$log
 * @param _$q
 * @returns {{response: handleResp, responseError: handleRespErr}}
 */
export default angular.module(`${conf.app}.config.http`, [])
    .config(confNormalizeHttpResp)
    .config(confHttp);



